import React, { useEffect, useState } from 'react'

import Item from '../components/Builds/Item'

import {
    Grid,
    TextField
} from '@material-ui/core'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faFilter
} from '@fortawesome/free-solid-svg-icons'

import { makeStyles } from '@material-ui/core/styles'

import { colors } from '../styles/theme'

const ItemList = (props) => {
    const {
        items,
        section,
        setData
    } = props

    const [filteredItems, setFilteredItems] = useState(items)

    useEffect(() => {
        setFilteredItems(props.items)
    }, [props.items])

    const toggleFilter = () => {
        
    }

    const filterItems = (filter) => {
        if(filter !== undefined && filter.length > 0) {
            const filteredList = items.filter(i => i.name.toUpperCase().includes(filter.toUpperCase()))
            setFilteredItems(filteredList)
        } else {
            setFilteredItems(items)
        }
    }

    const useStyles = makeStyles({
        root: {
          "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
            border: `1px solid ${colors.theme.grey}`,
            color: colors.theme.white
          },
          "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
            border: `1px solid ${colors.theme.grey}`,
            color: colors.theme.white
          },
          "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
            border: `1px solid ${colors.theme.grey}`,
            color: colors.theme.white
          },
          "& .MuiOutlinedInput-input": {
            padding: 12
          }
        },
        
        input: {
          color: colors.theme.white
        },
      })
    
      const classes = useStyles()

    return (
        <Grid container
            alignContent='center'
            alignItems='center'
            style={styles.section}>
            <Grid item xs={6}></Grid>
            <Grid item xs={6}>
                <Grid container
                    justify="center"
                    alignItems="center"
                    alignContent="center">
                    <Grid item xs={10}>
                        <TextField 
                            style={styles.searchField}
                            className={ classes.root }
                            InputProps={{ className: classes.input }}
                            fullWidth
                            variant="outlined"
                            placeholder="Search Items"
                            onChange={(e) => filterItems(e.target.value)}
                        />
                    </Grid>
                    <Grid item xs={1} />
                    <Grid item xs={1}
                        style={styles.itemFilter}>
                        <Grid container
                            justify="center"
                            alignContent="center"
                            alignItems="center"
                            direction="column">
                            <Grid item>
                                <FontAwesomeIcon icon={faFilter} color="white" 
                                    onClick={toggleFilter} />
                            </Grid>
                            <Grid item
                                style={styles.label}>
                                Filter
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
            <Grid container 
                direction="row"
                justify="space-evenly"
                alignItems="flex-begin"
                style={styles.scrollableList}>
                { filteredItems && filteredItems.map((item, index) => 
                    <Grid item
                        style={styles.item}>
                            <Item key={`item-${index}`} section={section} item={item} setData={setData} />
                    </Grid>
                )}
            </Grid>
        </Grid>
    )
}

const styles = {
    section: {
        color: colors.theme.white
    },
    label: {
        color: colors.theme.white,
        fontSize: "0.8em"
    },
    scrollableList: {
        maxHeight: 400,
        overflow: "auto",

        marginTop: 20,
        marginRight: 15
    },
    itemList: {
        margin: 15,
        width: "100%",

        backgroundColor: colors.theme.sectionBackground
    },
    item: {
        backgroundColor: colors.theme.black,
        padding: 1,
        paddingTop: 10,
        paddingBottom: 10,
        margin: 10,
        width: 100,
        textAlign: 'center'
    },

    searchField: {
        marginTop: 10
    },

    itemFilter: {
        opacity: 0.2,
        cursor: "not-allowed"
    }
} 

export default ItemList