import React from 'react';

import BuildView from '../../../components/BuildView'

import {
  Button,
  Grid
} from '@material-ui/core'

import { colors } from '../../../styles/theme'

const FragmentBuildReview = (props) => {
  const {
    data
  } = props

  console.log(data)

  return (
    <Grid container 
      alignContent='center'
      alignItems='center'>
        <Grid item xs={12}>
          <Button>Save Build</Button>
        </Grid>
        <Grid item xs={12}>
          <BuildView data={data} />
        </Grid>
        <Grid item xs={12}>
          <Button>Save Build</Button>
        </Grid>
    </Grid>
  )
}

export default FragmentBuildReview;

const styles = {
  header: {
    color: colors.gray
  },
  todo: {
      color: colors.theme.white
  }
}