import React from 'react';

import {
  Grid
} from '@material-ui/core'

import { colors } from '../../../styles/theme'

const FragmentBuildPairings = () => {
  return (
    <Grid container 
      alignContent='center'
      alignItems='center'
      style={{minHeight:400, alignContent: 'start'}}>
        <Grid item xs={12}>
          <h3 style={styles.header}>Coming Soon<sup style={{fontSize:10}}>TM</sup></h3>
        </Grid>
    </Grid>
  )
}

export default FragmentBuildPairings;

const styles = {
  header: {
    color: colors.gray
  }
}