import axios from 'axios'

// Production
//const API_DOMAIN = 'https://ethereanggapi.azurewebsites.net/api'
const API_DOMAIN = 'https://localhost:44390/api'

export const GET_FAMILIARS = "GET/familiars"
export const GET_ITEMS = "GET/items"
export const GET_MYTHS = "GET/myths"

export const getBuild = async (id) => {
    try {
        const response = await axios.get(`${API_DOMAIN}/builds/${id}`)
        return response.data
    } catch(e) { }
}

export const getBuildList = async (filter) => {
    try {
        // do something with filter...
        let filterQuery = "&"

        const response = await axios.get(`${API_DOMAIN}/builds?${filterQuery}`)
        return response.data
    } catch(e) { }
}

