import React from 'react'

import {
    Grid
} from '@material-ui/core'

export const SectionHeaderItem = (props) => {
    const {
        image
    } = props

    return (
        <Grid container
            justify="center"
            alignItems="center" >
            <Grid item xs={2}
                style={{
                    backgroundImage: `url(${image})`,
                    backgroundSize: "100%",
                    backgroundRepeat: "no-repeat",
                    minWidth: 40,
                    minHeight: 40,
                    height: "100%",
                    width: "100%",
                }}>
            </Grid>
        </Grid>
    )
}

export default SectionHeaderItem