import React, { useEffect, useState } from 'react';

import {
  Grid,
  MenuItem,
  Select,
  Typography
} from '@material-ui/core'

import { colors } from '../../../styles/theme'

import { makeStyles } from '@material-ui/core/styles'

import {
  SelectedStat,
  StatPicker
} from '../../../components/Builds'

import {
  getCombatTacticsList
} from '../../../state/actions/ethereal.actions'

const FragmentBuildLoadout = (props) => {
  const {

  } = props

  const [remainingPoints, setRemainingPoints] = useState(8)
  
  const [combatTactics, setCombatTactics] = useState([])
  const [secondaryStats, setSecondaryStats] = useState([])

  const [selectedStats, setSelectedStats] = useState([])
  const [selectedSecondaryStat, setSelectedSecondaryStat] = useState({})

  const [selectedPrimaryTactic, setSelectedPrimaryTactic] = useState(-1)
  const [selectedSecondaryTactic, setSelectedSecondaryTactic] = useState(-1)

  const statCosts = [1, 2, 3, 4]
  const statColors = [colors.theme.statsFirst, colors.theme.statsSecond, colors.theme.statsThird, colors.theme.statsFourth]
  
  useEffect(() => {
    const retrieveCombatTactics = async (id) => {
      //setCombatTactics(await getCombatTacticsList())
      setCombatTactics([{
          name: 'Assault',
          stats: ['Attack Speed', 'Life Steal', 'Penetration']
        },{
          name: 'Demolition',
          stats: ['Structure Damage', 'Move Speed', 'Spell Vamp']
        },{
          name: 'Protect',
          stats: ['Health', 'Defense', 'Persistence']
        },{
          name: 'Reinforcement',
          stats: ['Defensive Power', 'Cooldowns', 'Mana']
        }
      ])
    }

    retrieveCombatTactics()
  }, [])

  const handleSelectedTactic = (event) => {
    setSelectedPrimaryTactic(event.target.value)
    setRemainingPoints(8)
    setSelectedStats([])

    let statList = []
    combatTactics.map((tactic, index) => {
      if(index !== event.target.value) {
        tactic.stats.map((stat) => {
          statList.push({
            tactic: tactic.name,
            stat: stat
          })
        })
      }
    })

    setSecondaryStats(statList)
    setSelectedSecondaryTactic(0)
    setSelectedSecondaryStat(statList[0])
  }

  const handleSelectedSecondary = (event) => {
    setSelectedSecondaryTactic(event.target.value)
    setSelectedSecondaryStat(secondaryStats[event.target.value])

    const tempStats = selectedStats.filter(stat => stat.isPrimary)
    let tempCost = 8
    tempStats.map((stat) => {
      tempCost = tempCost - stat.cost
    })

    setSelectedStats(tempStats)
    setRemainingPoints(tempCost)
  }

  const addStat = (stat) => {
    let tempSelectedStats = selectedStats
    let tempRemainingPoints = remainingPoints

    tempSelectedStats.push({
      ...stat
    })

    setSelectedStats(tempSelectedStats)
    setRemainingPoints(tempRemainingPoints - stat.cost)
  }

  const removeStatAt = (index) => {
    let tempSelectedStats = selectedStats
    let tempRemainingPoints = remainingPoints

    const stat = tempSelectedStats[index]
    tempSelectedStats.splice(index, 1)

    setSelectedStats(tempSelectedStats)
    setRemainingPoints(tempRemainingPoints + stat.cost)
  }

  const useStyles = makeStyles({
    selectDropdown: {
      border: `1px solid ${colors.theme.white}`,
      color: colors.theme.white,
      width: '100%',

      ".MuiSvgIcon-root .MuiSelect-icon": {
        color: colors.theme.white
      }
    },

    menuItem: {
     marginLeft: 10 
    }
  })

  const classes = useStyles()

  return (
    <Grid container 
      alignContent='center'
      alignItems='center'>
        <Grid item xs={12} style={styles.tacticsContainer}>
          <Grid container style={{alignItems: 'center', alignContent: 'center' }}>
            <Grid item xs={7} style={{alignItems: 'center', alignContent: 'center'}}>
              <Typography style={styles.label}>1. Selected Combat Stats</Typography>
            </Grid>
            <Typography style={styles.label}>Stat Costs:</Typography>
            { statCosts.map((cost, index) => 
                  <Typography style={{
                      ...styles.statCosts,
                      backgroundColor: statColors[index], 
                      color: index < 3 ? colors.theme.black : colors.theme.white
                    }}>{cost}</Typography>
            )}
            <Grid item xs={2}>
              <Typography style={{...styles.label, textAlign: "right"}}>Remaining Points:</Typography>
            </Grid>
            <Grid item xs>
              <Typography style={styles.remainingPoints}>{remainingPoints}</Typography>
            </Grid>
          </Grid>
          
          <Grid container style={styles.statContainer}>
              { selectedStats.map((stat, index) => 
              <Grid item xs={3} style={styles.selectedStat}>
                <SelectedStat stat={stat} removeStat={() => removeStatAt(index)} />
              </Grid>
              )}
          </Grid>

          <Grid container>
            <Grid item xs>
              <Grid container>
                <Grid item xs={12}>
                  <Typography style={styles.label}>Primary Combat Tactic</Typography>
                </Grid>
                <Grid item xs={12}>
                  <Select
                    value={selectedPrimaryTactic}
                    onChange={handleSelectedTactic}
                    className={ classes.selectDropdown } 
                    inputProps={{ className:classes.menuItem }}>
                      { !(selectedPrimaryTactic >= 0) &&
                      <MenuItem 
                        className={classes.menuItem}
                        value={-1}>Pick Primary Tactic</MenuItem>
                      }
                    { combatTactics && combatTactics.map((tactic, index) =>
                      <MenuItem className={classes.menuItem}
                        value={index}>{tactic.name}</MenuItem>
                    )}
                  </Select>
                </Grid>
                <Grid item xs={12}>
                  <Grid container>
                    { combatTactics[selectedPrimaryTactic] && combatTactics[selectedPrimaryTactic].stats.map((stat, index) => 
                      <Grid item xs={12}>
                        <StatPicker isPrimary={true} 
                          remainingPoints={remainingPoints}
                          name={stat} 
                          addStat={addStat} />
                      </Grid>
                    )}
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={1}></Grid>
            <Grid item xs>
              <Grid container>
                <Grid item xs={12}>
                  <Typography style={styles.label}>Secondary Combat Tactic</Typography>
                </Grid>
                <Grid item xs={12}>
                  <Select
                    disabled={selectedPrimaryTactic === -1}
                    width={1}
                    value={selectedSecondaryTactic}
                    onChange={handleSelectedSecondary}
                    className={ classes.selectDropdown }
                    inputProps={{ className:classes.menuItem }}
                    >
                      { selectedPrimaryTactic === -1 &&
                      <MenuItem />
                      }
                      { secondaryStats && secondaryStats.map((stat, index) =>
                      <MenuItem value={index}>{`[${stat.tactic}] ${stat.stat}`}</MenuItem>
                      )}
                  </Select>
                </Grid>
                <Grid item xs={12}>
                  <Grid container>
                    <Grid item xs={12}>
                      { selectedSecondaryTactic >= 0 &&
                      <StatPicker isPrimary={false} 
                        name={selectedSecondaryStat.stat} 
                        remainingPoints={remainingPoints}
                        addStat={addStat} />
                      }
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12} style={styles.familiarContainer}>
          <Grid container>
            <Grid item xs={12} style={{alignItems: 'center', alignContent: 'center'}}>
              <Typography style={styles.label}>2. Select Familiar</Typography>
            </Grid>
            <Grid item xs={12}>
              <Grid container>
                <Grid item xs={1} style={{ 
                    background: "no-repeat center/150% url('https://undyinggames.com/assets/Images/News/FamiliarCard.png')", 
                    border: `1px solid ${colors.theme.goldPrimary}`,
                    marginTop: 10,
                    height: 150, 
                    width: 250 }}>
                  
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
    </Grid>
  )
}

export default FragmentBuildLoadout;

const styles = {
  header: {
    color: colors.gray
  },
  label: {
    font: 10,
    color: colors.theme.white,
    marginRight: 2
  },

  tacticOption: {
    paddingLeft: 10,
    color: colors.theme.white
  },

  familiarContainer: {
    marginTop: 25
  },

  remainingPoints: {
    background: colors.theme.goldPrimary,
    color: colors.theme.black,
    
    width: 25,
    marginLeft: 10,
    textAlign: "center",
    alignContent: "center",
    alignItems: "center",
    
    border: 1,
    borderRadius: 3,
    borderColor: colors.white
  }, 
  statCosts: {
    border: `1px solid ${colors.theme.goldBackground}`,
    borderRadius: 5,
    width: 20, 
    margin: 5, 
    marginRight: 2, 
    
    textAlign: "center",
    alignContent: "center",
    alignItems: "center"
  },
  statContainer: {
    minHeight: 175,
    marginTop: 15,
    marginBottom: 25
  },
  selectedStat: {
    height: 75,
    marginTop: 5,
    marginBottom: 5,
    paddingLeft: 15,
    paddingRight: 15
  }
}