import React, { useEffect, useState } from 'react'

import {
    Button,
    Grid, 
    Typography
} from '@material-ui/core'

import { colors } from '../../styles/theme'

export const StatPicker = (props) => {
    const {
        name,
        isPrimary,
        remainingPoints,
        addStat
    } = props

    const [splitCosts, setSplitCosts] = useState([])
    const [splitColors, setSplitColors] = useState([])

    useEffect(() => {
        if(isPrimary) {
            setSplitCosts(statCosts.splice(0, 3))
            setSplitColors(statColors.splice(0, 3))
        } else {
            setSplitCosts(statCosts.splice(1, 3))
            setSplitColors(statColors.splice(1, 3))
        }
    }, [])

    const statPoints = [1, 4, 10]
    const statCosts = [1, 2, 3, 4]
    const statColors = [colors.theme.statsFirst, colors.theme.statsSecond, colors.theme.statsThird, colors.theme.statsFourth]

    return (
        <>
        { name &&
        <Grid container style={styles.pickerRow}>
            <Grid item xs={6}>
                <Typography style={styles.label}>
                    {name}
                </Typography>
            </Grid>
            { statPoints.map((points, index) => 
            <Grid item xs={2} style={{border: `1px solid ${splitColors[index]}`}}>
                <Button
                    disabled={remainingPoints - splitCosts[index] < 0}
                    onClick={() => addStat({
                        name: name, 
                        points: points, 
                        cost: splitCosts[index],
                        color: splitColors[index],
                        isPrimary: isPrimary
                    })}>
                <Typography
                    style={{color: splitColors[index]}}>
                + {points}
                </Typography>
                </Button>
            </Grid>
            )}
        </Grid>
        }
        </>
    )
}

export default StatPicker

const styles = {
    label: {
      color: colors.theme.white
    },
    pickerRow: {
        border: `1px solid ${colors.theme.goldPrimary}`,
        alignItems: 'center',
        
        paddingLeft: 15,
        marginTop: 5,
        marginBottom: 5
    }
  }