import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom'

import {
  Grid
} from '@material-ui/core'

import { getBuild } from '../../state/actions/ethereal.actions'

const ViewBuild = () => {
  document.title = "Etherean.gg - View Build"

  const { buildId } = useParams()
  const [build, setBuild] = useState({})

  useEffect(() => {      
    // browser throws an error if you don't define it and then call it \_()_/
    const retrieveBuild = async (id) => {
      setBuild(await getBuild(id))
    }

    retrieveBuild(buildId)
  }, [])

  return (
    build?.id ? 
    <Grid container>
      <div style={{color: "white"}}>
        ID: {build.id}<br />
        Props: {build.prop}
      </div>
    </Grid> :
    <Grid>
      No Build Found
    </Grid>
  );
}

export default ViewBuild;