import { createReducer} from '@reduxjs/toolkit'

import {
    GET_FAMILIARS,
    GET_ITEMS,
    GET_MYTHS
} from './../actions/ethereal.actions'

const initialState = {
    isFetching: false
}

// Not used, redux may be over complicating this?
const etherealReducer = createReducer(initialState, {
    [GET_FAMILIARS]: (state, action) => {
        state.familiars = action.payload
    },

    [GET_ITEMS]: (state, action) => {
        state.items = action.payload
    },

    [GET_MYTHS]: (state, action) => {
        state.myth = action.payload
    }
});

export default etherealReducer;