import React from 'react'

import {
    Grid, 
    Typography
} from '@material-ui/core'

import colors from '../../styles/theme'

export const StaticAbility = (props) => {
    const {
        ability
    } = props

    // TODO: Implement Hover details?
    return (
        <Grid container>
            <Grid item xs={12}>
                <Grid container>
                    <Grid item xs={2}>
                        <img style={styles.image}
                            src={ability.imageURL} />
                    </Grid>
                    <Grid item xs>
                        <Grid container>
                            <Grid item xs={12}>
                                <Typography style={styles.name}>
                                    {ability.name}
                                </Typography>
                            </Grid>
                            <Grid item xs={12}>
                                <Typography style={styles.slot}>
                                    {ability.slot}
                                </Typography>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    )
}

export default StaticAbility

const styles = {
    image: {
        border: `1px solid ${colors.theme.goldPrimary}`,
        height: 75,
        width: 75
    },
    name: {
        fontSize: 15,
        color: colors.theme.white
    },
    slot: {
        fontSize: 10,
        color: colors.theme.white
    }
}