import React, { useState } from 'react';

import {
  Grid,
  TextField
} from '@material-ui/core'

import { makeStyles } from '@material-ui/core/styles'
import { colors } from '../../../styles/theme'

import {
  DynamicAbility,
  StaticAbility
} from '../../../components/Builds'

const FragmentBuildAbilities = (props) => {
  const {
    data,
    setData
  } = props

  console.log(data)

  // Hacking a state change :(
  const [count, setCount] = useState(0)

  const selectLevel = (abilityIndex, level, selected) => {
    let tempData = data

    tempData.map((_, index) => {
      tempData[index].levels = tempData[index].levels.filter(i => i !== level)
    })

    if(selected) {
      tempData[abilityIndex].levels.push(level)
    }

    setData(tempData)
    setCount(count+1)
  }

  const saveNotes = (notes) => {
    let tempData = data;
    tempData.notes = notes;

    setData(tempData)
    setCount(count+1)
  }

  const useStyles = makeStyles({
    root: {
      "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      },
      "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      },
      "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      },
      "& .MuiOutlinedInput-input": {
        padding: 12
      }
    },
    
    input: {
      color: colors.theme.white
    },
  })

  const classes = useStyles()

  return (
    <Grid container 
      alignContent='center'
      alignItems='center'>
        <Grid item xs={12}>
          <Grid container>
            <Grid item xs={6}>
              { data && data[0] && 
              <StaticAbility ability={data[0]} />
              }
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <Grid container>
            { data && data.slice(1, 7).map((ability, index) =>
             <Grid item xs={12} style={styles.dynamicAbilities}
                key={`ability-${index}`}>
                <DynamicAbility ability={ability} abilityIndex={index+1} selectLevel={selectLevel} />
            </Grid>
            )}
          </Grid>
          <Grid item xs={12}>
            <TextField multiline rows={6} 
              variant="outlined"
              style={styles.notes} 
              className={ classes.root }
              InputProps={{
                className: classes.input
              }}
              placeholder="Notes..."
              defaultValue={data?.notes}
              onChange={(e) => { 
                saveNotes(e.target.value)
              }} />
          </Grid>
        </Grid> 
    </Grid>
  )
}

export default FragmentBuildAbilities;

const styles = {
  header: {
    color: colors.gray
  },
  dynamicAbilities: {
    marginTop: 35,
    marginBottom: 10
  },

  notes: {
    width: "100%",

    marginTop: 15
  }
}