import React from 'react'

import {
    Grid,
    Typography
} from '@material-ui/core'

import colors from '../../styles/theme'

export const Item = (props) => {
    const {
        item,
        section,
        setData
    } = props

    return (
        <Grid container
            direction="column"
            justify="center"
            alignItems="center"
            onClick={() => setData(section, item)}
            style={styles.item}>
            <Grid item xs={2}
                style={{
                    backgroundImage: `url(${item.imageURL})`,
                    backgroundSize: "100%",
                    backgroundRepeat: "no-repeat",
                    minWidth: 75,
                    minHeight: 75,
                    height: "100%",
                    width: "100%",
                }}>
                <Grid container>
                    <Grid item xs />
                    <Grid item xs={2}>
                        <img src={`${process.env.PUBLIC_URL}/images/currency_gold.png`} width={10} height={10} alt="currency - gold" />
                    </Grid>
                    <Grid item xs={5} style={styles.itemCost}>
                        <Typography>
                            {item.cost}
                        </Typography>
                    </Grid>
                    <Grid item xs />
                </Grid>
            </Grid>
            <Grid item xs
                style={styles.itemName}>
                {item.name}
            </Grid>
        </Grid>
    )
}

export default Item

const styles = {
    item: {
        cursor: "pointer"
    },
    itemName: {
        color: colors.theme.white,

        marginTop: 10,
        fontSize: 12
    },
    itemCost: {
        background: `1px solid ${colors.theme.goldBackground}`,

        margin: 0,
        padding: 0,
        width: "100%",
        height: "100%"
    }
}