import React from 'react'

import {
    Button,
    Grid,
    Typography
} from '@material-ui/core'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faTrashAlt
} from '@fortawesome/free-solid-svg-icons'

import { colors } from '../../styles/theme'

export const SelectedStat = (props) => {
    const {
        stat,
        removeStat
    } = props

    return (
        <Grid container style={styles.container}>
            <Grid item xs={1} style={{
                    ...styles.statCost, 
                    backgroundColor: stat.color,
                    color: stat.cost === 4 ? colors.theme.white : colors.theme.black
                }}>
                <Typography>
                    {stat.points}
                </Typography>
            </Grid>
            <Grid item xs={8}>
                <Typography style={styles.statName}>
                    {stat.name} ({stat.cost})
                </Typography>
            </Grid>
            <Grid item xs={1}
                style={styles.deleteIcon}>
                <Button onClick={removeStat}>
                    <FontAwesomeIcon
                        icon={faTrashAlt} color="white" size="sm" />
                </Button>
            </Grid>
        </Grid>
    )
} 

export default SelectedStat

const styles = {
    container: {
        border: `1px solid ${colors.theme.goldPrimary}`,
        padding: 10,
        borderRadius: '7px 0',

        alignItems: 'center'
    },

    statName: {
        color: colors.theme.goldPrimary,
        paddingLeft: 10,
        alignItems: 'center',
        alignContent: 'center'
    },
    statCost: {
        textAlign: 'center',
        border: `1px solid ${colors.theme.goldPrimary}`,
        borderRadius: 7,
        width: 15,
        margin: 2
    },

    deleteIcon: {
        alignItems: 'flex-end',
        justifyContent: 'flex-end'
    }
}