import React from 'react';
import routes from './routes'
import { Switch } from 'react-router-dom'
import RouteAndSubRoutes from './routes/RouteAndSubRoutes'

import { BrowserRouter } from 'react-router-dom'  
import { Provider } from 'react-redux'

import store, { persistor } from './state'
import { PersistGate } from 'redux-persist/integration/react'

import { makeStyles } from '@material-ui/core/styles'
import { colors } from './styles/theme'

import { library } from '@fortawesome/fontawesome-svg-core'
import { 
  faArrowsAlt,
  faFilter,
  faTrashAlt 
} from '@fortawesome/free-solid-svg-icons'

library.add(
  faArrowsAlt,
  faTrashAlt,
  faFilter
)

const AppBase = () => {
  const useStyles = makeStyles({
    '@global': {
        '*::-webkit-scrollbar': {
          width: '0.4em'
        },
        '*::-webkit-scrollbar-track': {
          '-webkit-box-shadow': `inset 0 0 6px ${colors.theme.scrollLane}`
        },
        '*::-webkit-scrollbar-thumb': {
          backgroundColor: colors.theme.scrollBar,
          outline: `1px solid ${colors.theme.scrollBar}`
        }
    },
    root: {
      "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      },
      "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      },
      "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      }
    },
    
    input: {
      color: colors.theme.white
    },
  })

  useStyles()

  return (
    <div name="wrapper" style={styles.wrapper}>
      <div name="content" style={styles.content}>
        <React.StrictMode>
          <Provider store={store}>
            <PersistGate
              loading="Loading..."
              persistor={persistor}
            >
              <BrowserRouter>
                <Switch> {
                  routes.map(
                    (route, index) => 
                      <RouteAndSubRoutes key={index} {...route} />
                    )
                }
                </Switch>
              </BrowserRouter>
            </PersistGate>
          </Provider>
        </React.StrictMode>
      </div>
    </div>
  )
}

const styles = {
  wrapper: {
    height: "100%",

    backgroundColor: colors.theme.siteBackground,
  },
  content: {
    margin: "auto",
    padding: "4vh",

    height: "100%",
    minHeight: "92vh",
    maxWidth: "1000px",

    backgroundColor: colors.theme.contentBackground,
    color: colors.theme.white,
  }
}

export default AppBase;