import React, { useEffect, useState } from 'react';

import CollapsibleItemSection from '../../../components/CollapsibleItemSection'
import ItemList from '../../../components/ItemList'
import Item from '../../../components/Builds/Item'

import {
  Button,
  Grid,
  TextField
} from '@material-ui/core'

import { makeStyles } from '@material-ui/core/styles'

import { colors } from '../../../styles/theme'

import {
  getItemList
} from '../../../state/actions/ethereal.actions'

const FragmentBuildItems = (props) => {
  const {
    data,
    setData
  } = props

  const [count, setCount] = useState(0)
  const [itemList, setItemList] = useState([])

  const addSection = () => {
    let tempSections = data
    const id = Math.max(...tempSections.map(s => s.id), 0);
    console.log(id)
    
    tempSections = tempSections.concat({ id: id+1, order: id+1, build: [], notes: ''})

    setData(tempSections)
    setCount(count+1)
  }

  
  const deleteSection = (sectionId) => {
    console.log(sectionId)
    let tempSections = data
    tempSections = data.filter(s => s.id != sectionId)

    setData(tempSections)
    setCount(count+1)
  }

  const addItem = (index, item) => {
    let tempSections = data

    const hasBoots = tempSections[index].build.some(b => b.name.endsWith('Boots')) || item.name.endsWith('Boots');

    if(tempSections[index].build.length >= (hasBoots ? 7 : 6)) { return }
    if(tempSections[index].build.filter(i => i.name === item.name).length > 0) { return }

    tempSections[index].build.push(item)
    setData(tempSections)
    setCount(count+1)
  }

  const removeItem = (index, item) => {
    let tempSections = data
    tempSections[index].build = tempSections[index].build.filter(i => i.name !== item.name)

    setData(tempSections)
    setCount(count+1)
  }

  const saveTitle = (index, title) => {
    let tempSections = data

    tempSections[index].title = title

    setData(tempSections)
    setCount(count+1)
  }

  const saveNotes = (index, notes) => {
    let tempSections = data
    tempSections[index].notes = notes

    setData(tempSections)
    setCount(count+1)
  }

  useEffect(() => {
    const retrieveItemList = async () => {
      setItemList(await getItemList())
    }

    retrieveItemList()
  }, [])

  const useStyles = makeStyles({
    root: {
      "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      },
      "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      },
      "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      },
      "& .MuiOutlinedInput-input": {
        padding: 12
      }
    },
    
    input: {
      color: colors.theme.white
    },
  })

  const classes = useStyles()

  return (
    <Grid container 
      alignContent='center'
      alignItems='center'>
      <Grid item xs={12}>
        <Button style={styles.buttonAddSection}
          onClick={() => addSection()}>
            ADD A SECTION
        </Button>
      </Grid>
      { data.map((section, sIndex) => 
        <CollapsibleItemSection 
          key={`sections-items-${section.id}`}
          style={styles.section}
          sectionIndex={sIndex}
          sectionId={section.id}
          title={section.title}
          defaultExpanded={data.length == 1}
          data={section.build}
          saveTitle={saveTitle}
          deleteSection={deleteSection}>
            <Grid container>
              { section.build.map((item, iIndex) => 
                <Grid item xs={1} style={styles.selectedItem}>
                  <Item
                    key={`itemsection-${sIndex}-item-${iIndex}`}
                    section={sIndex} 
                    item={item} 
                    setData={removeItem} />
                </Grid>
              )}
              <Grid item xs />
              <Grid item xs={12}>
                <ItemList key={`item-list-${sIndex}`} items={itemList} section={sIndex} build={section.build} setData={addItem} />
              </Grid>
              <Grid item xs={12}>
                <TextField multiline rows={6} 
                  variant="outlined"
                  style={styles.notes} 
                  className={ classes.root }
                  InputProps={{
                    className: classes.input
                  }}
                  placeholder="Notes..."
                  defaultValue={section.notes}
                  onChange={(e) => { 
                    saveNotes(sIndex, e.target.value)
                  }} />
              </Grid>
            </Grid>
        </CollapsibleItemSection>
      )}
      <Grid item xs={12}>
        <Button style={styles.buttonAddSection}
          onClick={() => addSection()}>
            ADD A SECTION
        </Button>
      </Grid>
    </Grid>
  )
}

export default FragmentBuildItems;

const styles = {
  header: {
    color: colors.gray
  },
  section: {
    marginBottom: 10
  },
  buttonAddSection: {
    backgroundColor: colors.theme.goldPrimary,
    fontSize: "0.7em",
    marginTop: 10,
    marginBottom: 15
  },

  selectedItem: {
    marginRight: 20,
    color: colors.theme.white
  },
  notes: {
    width: "100%",

    marginTop: 15
  }
}