import CreateBuild from '../pages/builds/Create'
import ViewBuild from '../pages/builds/View'
import NotFound from '../pages/NotFound'

const routes = [
  { path: "", key: "ROOT", exact: true, component: CreateBuild },

  // Builds
  { path: "/builds/create", key: "CREATE_BUILD", exact: true, component: CreateBuild },
  { path: "/builds/view", key: "VIEW_BUILD_LIST", exact: true, component: ViewBuild },
  { path: "/builds/view/:buildId", key: "VIEW_BUILD_ID", component: ViewBuild },

  // Catch All Else
  { path: "/:NotFound", component: NotFound }
];

export default routes;