import React from 'react'

import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Button,
  Grid,
  TextField
} from '@material-ui/core'

import SectionHeaderItem from './Builds/SectionHeaderItem'

import { makeStyles } from '@material-ui/core/styles'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faArrowsAlt,
  faTrashAlt
} from '@fortawesome/free-solid-svg-icons'

import colors from '../styles/theme'

import ExpandMoreIcon from '@material-ui/icons/ExpandMore'

const CollapsibleItemSection = (props) => {
    const {
      title,
      data,
      sectionIndex,
      sectionId,
      saveTitle,
      deleteSection,
      defaultExpanded
    } = props

    console.log(data)

    const useStyles = makeStyles({
      root: {
        "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
          border: `1px solid ${colors.theme.grey}`,
          color: colors.theme.white
        },
        "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
          border: `1px solid ${colors.theme.grey}`,
          color: colors.theme.white
        },
        "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
          border: `1px solid ${colors.theme.grey}`,
          color: colors.theme.white
        },
        "& .MuiOutlinedInput-input": {
          padding: 12
        }
      },
      
      input: {
        color: colors.theme.white
      },
    })
  
    const classes = useStyles()

    return (
      <Grid container
        style={styles.section}
        alignContent='center'
        alignItems='center'>
        <Grid item xs={12}>
          <Accordion style={{borderRadius: 5}} 
            defaultExpanded={defaultExpanded}>
            <AccordionSummary
              style={styles.summary}
              expandIcon={<ExpandMoreIcon style={{ fill: colors.theme.white }} />} >
              <Grid container
                onClick={(event) => event.stopPropagation()}
                onFocus={(event) => event.stopPropagation()} 
                alignItems="center"
                direction="row"
                >
                <Grid item xs={1}
                  style={{marginRight: -10}}>
                  <FontAwesomeIcon
                    styles={styles.faDragIcon}
                    icon={faArrowsAlt} color="white" size="lg" />
                </Grid>
                <Grid item xs={5}>
                  <TextField 
                    placeholder="Title"
                    className={ classes.root }
                    InputProps={{
                      className: classes.input
                    }}
                    fullWidth
                    variant="outlined"
                    defaultValue={title}
                    onChange={(e) => { 
                      saveTitle(sectionIndex, e.target.value)
                    }}
                  />
                </Grid>
                <Grid item xs>
                  <Grid container style={styles.sectionHeaderItemsContainer} >
                  { data && data.map((item, index) => 
                    <Grid item style={styles.sectionHeaderItem}>
                      <SectionHeaderItem key={`itemheader--${index}`} image={item.imageURL} />
                    </Grid>
                  )}
                  </Grid>
                </Grid>
                <Grid item xs={1}
                  direction="row-reverse">
                    <Button 
                        style={styles.deleteSectionButton}
                        onClick={() => deleteSection(sectionId)}>
                      <Grid container
                        direction="column"
                        style={styles.faTrashIcon}>
                        <Grid item>
                          <FontAwesomeIcon
                          styles={styles.faTrashIcon}
                          icon={faTrashAlt} color="white" size="lg" />
                        </Grid>
                        <Grid item
                          style={styles.label}>
                          Delete
                        </Grid>
                      </Grid>
                  </Button>
                </Grid>
              </Grid>
            </AccordionSummary>
            <AccordionDetails style={styles.details}>
              {props.children}
            </AccordionDetails>
          </Accordion>
        </Grid>
      </Grid>
    )
  }
  
export default CollapsibleItemSection;


const styles = {
  section: {
    marginBottom: 10
  },
  deleteSectionButton: {
    margin: 0,
    padding: 0,
    color: colors.theme.white
  },
  sectionHeaderItemsContainer: {
    justifyContent: 'flex-start'
  },
  sectionHeaderItem: {
    paddingLeft: 10
  },
  summary: {
    backgroundColor: colors.theme.sectionHeaderBackground,

    height: 65
  },
  details: {
    backgroundColor: colors.theme.sectionBackground,
  },
  label: {
    color: colors.theme.white,
    fontSize: "0.8em"
  },
  faDragIcon: {
    alignContent: "center",
    alignItems: "center",
    justify: "center"
  },
  faTrashIcon: {
    alignContent: "center",
    alignItems: "center",
    justify: "center"
  }
}